<template>
  <div>
    <div class="title">查询队伍</div>
    <el-row :gutter="50">
      <el-col :span="8">
        <el-input
          v-model="team_name"
          placeholder="请输入战队名字搜寻ID"
        ></el-input>
      </el-col>
      <el-col :span="3">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-col>
      <el-col :span="8">
        <el-select
          v-model="eventname"
          style="margin-bottom: 10px; margin-right: 30px"
          placeholder="请选择赛季"
        >
          <el-option
            v-for="item in options"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <div class="title">
      列表展示<i id="icon-show" :class="iconToggle" @click="toggleIcon">{{
        showMsg
      }}</i>
    </div>
    <div v-show="isShowBox">
      <template v-if="teamIdList.length === 0">
        <div class="wrap">
          <span>暂无数据</span>
        </div>
      </template>
      <template v-else>
        <table border width="40%">
          <thead>
            <th>序号</th>
            <th>战队名</th>
          </thead>
          <tbody>
            <tr v-for="item in teamIdList" :key="item.id">
              <td>{{ item.id }}</td>
              <td>{{ item.name }}</td>
            </tr>
          </tbody>
        </table>
      </template>
    </div>
    <!-- 发布 -->
    <div class="title">发布冠军</div>
    <p class="info">
      <i class="el-icon-question"></i>
      如果不清楚战队的formId(请搜索)
    </p>
    <el-row :gutter="20" style="margin-bottom: 20px">
      <el-col :span="2">
        <el-input v-model="id" placeholder="formId"></el-input>
      </el-col>
      <el-col :span="20">
        <el-input
          :rows="4"
          v-model="chaMsg"
          type="textarea"
          placeholder="请输入冠军简介"
        ></el-input>
      </el-col>
    </el-row>
    <el-button type="success" style="width: 60%; margin: 0 20%" @click="submit"
      >发布</el-button
    >
    <!-- 展示 -->
    <div class="title">冠军列表</div>
    <el-collapse v-for="item in cham" :key="item.id">
      <el-collapse-item name="item.id">
        <template slot="title">
          <p class="headTitle">
            {{ item.events.name }}冠军<i class="el-icon-medal-1"></i>
          </p>
        </template>
        <div>
          <span class="chamTitle">冠军战队:</span>{{ item.form.team_name }}
        </div>
        <div>
          <span class="chamTitle hunter">监管者:</span>
          <span v-for="el in item.form.role" :key="el.id">
            <span v-if="el.role_lin === '监管者'">{{ el.role_name }}</span>
          </span>
        </div>
        <div>
          <span class="chamTitle survivor">求生者:</span>
          <span v-for="el in item.form.role" :key="el.id">
            <span v-if="el.role_lin === '求生者'">{{ el.role_name }}、</span>
          </span>
        </div>
        <div><span class="chamTitle">战队简介:</span>{{ item.msg }}</div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import { searchId, saveCham, getAllCham } from "@/api/cham/index.js";
import { getAllEvents } from "@/api/gameSeason/index";

export default {
  name: "Blok",
  data() {
    return {
      id: null,
      team_name: "",
      teamIdList: [],
      iconToggle: "el-icon-download",
      isShowBox: false,
      showMsg: "打开",
      options: [],
      eventname: "",
      chaMsg: "", //冠军简介
      cham: [],
    };
  },
  methods: {
    search() {
      searchId(this.team_name)
        .then((res) => {
          this.teamIdList = res.data;
          this.isShowBox = true;
          this.iconToggle = "el-icon-upload2";
          this.showMsg = "折叠";
        })
        .catch(() => {
          this.$message.error("服务器异常，请联系管理员");
        });
    },
    //切换公告
    toggleIcon() {
      if (this.iconToggle === "el-icon-download") {
        this.isShowBox = true;
        this.iconToggle = "el-icon-upload2";
        this.showMsg = "折叠";
      } else {
        this.isShowBox = false;
        this.iconToggle = "el-icon-download";
        this.showMsg = "打开";
      }
    },
    // 获取赛季
    initSeason() {
      getAllEvents()
        .then((res) => {
          this.options = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //发布
    submit() {
      if (this.id === null && this.chaMsg === "") {
        this.$message.error("请输入冠军信息和formid");
      } else {
        const data = {
          formId: this.id,
          eventname: this.eventname,
          msg: this.chaMsg,
        };
        saveCham(data)
          .then(() => {
            this.$message.success("冠军发布成功！");
          })
          .catch((err) => {
            this.$message.error(err);
          });
      }
    },
    initAllCham() {
      getAllCham()
        .then((res) => {
          this.cham = res.data;
        })
        .catch((err) => {
          this.$message.error(err);
        });
    },
  },
  mounted() {
    this.initSeason();
    this.initAllCham();
  },
};
</script>

<style scoped lang="less">
table {
  border-collapse: collapse;
  margin-top: 20px;
}
.title {
  padding: 10px;
  margin: 10px 0;
  border-left: 5px solid #409eff;
  border-bottom: 1px solid #dfdfdf;
}
#icon-show {
  margin-left: 20px;
  font-size: 16px;
}
.info {
  margin: 5px 0;
  color: rgb(212, 185, 98);
}
.chamTitle {
  padding: 0 5px;
  border-left: 5px solid gold;
  margin: 5px 0;
}
.chamTitle.hunter {
  border-left: 5px solid red;
}
.chamTitle.survivor {
  border-left: 5px solid blue;
}
.headTitle {
  font-size: 18px;
  color: orange;
}
</style>
